import React, { Component } from 'react'
import axios from 'axios';
import classNames from 'classnames';
import LyricsModal from './lyricsModal';
import { Button } from 'react-bootstrap';

class VideoLoader extends Component {

    titles = {};
    lyrics = {};
    players = {};
    playingIndex = null;
    pausedIndex = null;
    volume = 80;

    constructor(props) {
        super(props);

        this.state = {
            videoIds: props.videoIds || [],
            players: [],
            showLyricModal: false,
            lyricIndex: null
        }

        if (typeof window !== 'undefined') {
            window.onYouTubePlayerAPIReady = this.onYouTubePlayerAPIReady;
            this.loadScript();
        }
    }

    loadScript = () => {
        if (document.getElementById('youTubeScript')) {
            setTimeout(() => this.onYouTubePlayerAPIReady());
            return;
        }
        var script = document.createElement('script');
        script.src = 'https://www.youtube.com/iframe_api';
        script.id = 'youTubeScript';
        script.onload = this.onScriptLoaded;
        var firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(script, firstScriptTag);
    }

    onScriptLoaded = () => {

    }

    prev = () => {
        const { players, showLyricModal } = this.state;
        let p;
        if (this.playingIndex === null) {
            return;
        } else {
            p = players[this.playingIndex];
        }

        if (p.index == 0) {
            return;
        }

        p.player.stopVideo();
        p.isPlaying = false;

        const prevPlayer = players[this.playingIndex - 1]
        prevPlayer.player.playVideo();
        this.playingIndex = prevPlayer.index;

        this.players[this.playingIndex].scrollIntoView({ behavior: 'smooth', block: 'center' })

        this.clearPaused();

        if (showLyricModal) {
            this.showLyrics(this.playingIndex);
        } else {
            this.forceUpdate();
        }
    }

    play = () => {
        let p;
        if (this.playingIndex === null) {
            p = this.state.players[0];
        } else {
            p = this.state.players.find(x => x.index === this.playingIndex);
        }

        p.player.playVideo();
        p.isPlaying = true;
        this.playingIndex = p.index;

        this.clearPaused();
        this.forceUpdate();
    }

    pause = () => {
        if (this.playingIndex === null) {
            return;
        } else if (this.playingIndex !== null && this.pausedIndex) {
            this.play();
            return;
        }

        let p = this.state.players[this.playingIndex];
        p.player.pauseVideo();
        p.isPlaying = false;

        this.pausedIndex = this.playingIndex;
    }

    clearPaused = () => {
        if (this.pausedIndex) {
            this.pausedIndex = null;
        }
    }

    stop = () => {
        let p;
        if (this.playingIndex === null) {
            p = this.players[0];
        } else {
            p = this.state.players.find(x => x.index === this.playingIndex);
        }

        p.player.stopVideo();
        p.isPlaying = false;
        //this.playingIndex = null;

        this.clearPaused();
        this.forceUpdate();
    }

    next = () => {
        const { players, showLyricModal } = this.state;
        let p;
        if (this.playingIndex === null) {
            return;
        } else {
            p = players[this.playingIndex];
        }

        if (p.index >= players.length - 1) {
            return;
        }

        p.player.stopVideo();
        p.isPlaying = false;

        const nextPlayer = players[this.playingIndex + 1]
        nextPlayer.player.playVideo();
        this.playingIndex = nextPlayer.index;

        this.players[this.playingIndex].scrollIntoView({ behavior: 'smooth', block: 'center' });

        this.clearPaused();

        if (showLyricModal) {
            this.showLyrics(this.playingIndex);
        } else {
            this.forceUpdate();
        }
    }

    setVolume = (e) => {
        if (this.playingIndex === null) {
            return;
        }

        const p = this.state.players[this.playingIndex];
        const v = e.target.value;

        p.player.setVolume(v);
        this.volume = v;

        this.state.players.forEach(x => x.player.setVolume(v));
    }

    getLyric = (title, index) => {
        axios.get(`https://cors-anywhere.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.search?q_track_artist=${title}&page_size=1&page=1&s_track_rating=desc&apikey=658a9bd36458276ed04fd0f3546f2b9c`)
            .then(res => {
                try {
                    var shareUrl = res.data.message.body.track_list[0].track.track_share_url;
                    shareUrl = shareUrl.substring(0, shareUrl.indexOf("?"));
                    if (shareUrl) {
                        //this.lyrics[index].style.textDecoration = "underline";
                        this.state.players.find(x => x.index === index).lyrics = shareUrl;
                        this.forceUpdate();
                    }
                }
                catch (error) {
                    console.error(`Error on ${title}`);
                }
            })
    }

    onYouTubePlayerAPIReady = () => {
        this.state.videoIds.forEach((x, i) => {

            function onPlayerReady(event, index) {
                const p = this.state.players[index];
                p.player.setVolume(this.volume);
                const title = event.target.playerInfo.videoData.title
                    .replace(/\[.*\]/g, '')
                    .replace(/\(.*\)/g, '')
                    .replace(/\"/g, '')
                    .replace(/\|.*$/, '');
                p.title = title;
                this.titles[index].textContent = title;
                this.getLyric(title, index);
            }

            function onPlayerStateChange(event, index) {
                if (event.data == window.YT.PlayerState.PLAYING) {
                    const p = this.state.players.find(x => x.index === index);

                    if (!p) {
                        return;
                    }

                    p.isPlaying = true;
                    this.playingIndex = p.index;

                    const playing = this.state.players.find(x => x.index !== index && x.isPlaying);
                    if (playing) {
                        playing.player.stopVideo();
                        playing.isPlaying = false;
                    }

                    this.forceUpdate()
                }
            }

            onPlayerReady = onPlayerReady.bind(this);
            onPlayerStateChange = onPlayerStateChange.bind(this);

            var p = new window.YT.Player(`player${i}`, {
                height: '200px',
                width: '360px',
                videoId: x,
                events: {
                    'onReady': (e) => onPlayerReady(e, i),
                    'onStateChange': (e) => onPlayerStateChange(e, i)
                },
                host: 'https://www.youtube.com',
                playerVars: {
                    'autoplay': 0,
                    'controls': 2,
                    'showinfo': 1,
                    'color': 'white',
                    'modestbranding': 0,
                    'loop': 0,
                    'fs': 0,
                    'cc_load_policy': 0,
                    'iv_load_policy': 3
                },
            })

            this.setState({
                players: this.state.players.concat([{
                    index: i,
                    player: p,
                    isPlaying: false
                }])
            })
        });
    }

    showLyrics = (index) => {
        const lyricUrl = this.state.players.find(x => x.index == index).lyrics;
        if (lyricUrl) {
            this.setState({
                showLyricModal: true,
                lyricIndex: index
            });
        } else {
            this.setState({
                showLyricModal: false,
                lyricIndex: null
            }, () => this.playingIndex && this.players[index].scrollIntoView({ behavior: 'smooth', block: 'center' }));
        }
    }

    onCloseLyrics = () => {
        this.setState({
            showLyricModal: false,
            lyricIndex: null
        }, () => this.playingIndex && this.players[this.playingIndex].scrollIntoView({ behavior: 'smooth', block: 'center' }));
    }

    renderPlayer = (player, index) => {
        const classes = classNames("vrc__playerContainer",
            { "vrc__playerContainer--playing": index === this.playingIndex }
        );
        return (
            <div className={classes} ref={r => this.players[index] = r} key={index}>
                <div className="vrc__playerWrapper" id={`player${index}`} />
                <div className="vrc__playerInfo">
                    <h4 ref={r => this.titles[index] = r} /><br />
                    <Button
                        variant="dark"
                        onClick={() => this.showLyrics(index)}
                        disabled={this.state.players[index] && !this.state.players[index].lyrics}
                    >
                        Lyrics
                    </Button>
                </div>
            </div>
        );
    }

    render() {
        const { title } = this.props;
        const { videoIds, showLyricModal, lyricIndex, players } = this.state;
        const lyricsPlayer = lyricIndex !== null && players.find(x => x.index == lyricIndex);
        return (
            <div ref={r => this.chartsContainer = r} className='vrc__playersContainer'>
                {title && <h1>{title}</h1>}
                {videoIds.map((x, i) => this.renderPlayer(x, i))}
                <div className="vrc__playerControls">
                    <div className="vrc__controlBtnContainer">
                        <div className="vrc__controlBtn vrc__controlBtn--prev" onClick={this.prev}>Prev</div>
                        <div className="vrc__controlBtn vrc__controlBtn--play" onClick={this.play}>Play</div>
                        <div className="vrc__controlBtn vrc__controlBtn--pause" onClick={this.pause}>Pause</div>
                        <div className="vrc__controlBtn vrc__controlBtn--stop" onClick={this.stop}>Stop</div>
                        <div className="vrc__controlBtn vrc__controlBtn--next" onClick={this.next}>Next</div>
                    </div>
                    <input
                        onChange={this.setVolume}
                        defaultValue={this.volume}
                        type="range"
                        id="vol"
                        name="vol"
                        min="0"
                        max="100"
                        className="vrc__playerRange" />
                </div>
                {(showLyricModal && lyricsPlayer !== undefined) && (
                    <LyricsModal
                        src={lyricsPlayer.lyrics}
                        title={lyricsPlayer.title}
                        onClose={this.onCloseLyrics}
                    />
                )}
            </div>
        )
    }
}

export default VideoLoader;